import React, {useState} from 'react'
import Navbar from '../navbar/Navbar'
import "./header.css"
import { Link } from 'react-scroll'


const Header = ({setShowMain, setShowBooking, setShowPricing, setShowRules, setShowSmaller, setShowBigger, setShowStudio, setShowContact}) => {
    return (
    <div className="header" id="head">
        <Navbar 
          setShowMain={setShowMain} setShowBooking={setShowBooking} setShowPricing={setShowPricing} 
          setShowRules={setShowRules} setShowSmaller={setShowSmaller} setShowBigger={setShowBigger}
          setShowStudio={setShowStudio} setShowContact={setShowContact}
        />
        <div className="landing-main">
        <div className="landing-title">Witamy na stronie sali prób oraz studia Próbówka Music House.</div>
        <div className="landing-content">Sala prób Próbówka Music House to miejsce stworzone przez muzyków dla muzyków. Znamy wasze potrzeby.
        <br></br> Miejsce jest klimatyzowane, ogrzewane, dostępne 24h na dobę, monitorowane, wyposażone w sprawdzony i zawsze działający sprzęt. Sala prób dysponuje także dużą ilością bezpłatnych miejsc parkingowych oraz toaletą. Oferujemy także możliwość bezpłatnego przechowywania swoich wzmacniaczy. 
        <br></br>
          Ponadto wraz z salami prób połączone jest profesjonalne studio nagrań. U nas bez problemu zarejestrujesz swoją próbę, nagrasz swój występ czy utrwalisz materiał na planowaną płytę. 
          <Link to="booking" smooth={true}><div className="landing-booking-button">REZERWACJA</div></Link>
          </div>
          
        </div>
        
          
        
    </div>
    )
}

export default Header
