import React, {useState} from 'react'
import studioo from '../studioo.png';

const Stuff = ({setBigger, setSmaller, setStudio}) => {
    return (
        <div className="stuff-main bg">
                <h3>Sale prób i studio nagrań</h3>
                <hr style={{width: '80px'}}></hr>
                <div className="stuff-content">
                    <div className="room" onClick={()=>{
                        setBigger(true)
                        setSmaller(false)
                        setStudio(false)
                        }}>
                        <div className="room-image"><img src={studioo} /></div>
                        <div className="room-title">Sala większa</div>
                        <hr style={{width: 100}}></hr>
                        <div className="room-description">Sala większa</div>
                    </div>
                    <div className="room" onClick={()=>{
                        setBigger(false)
                        setSmaller(true)
                        setStudio(false)
                        }}>
                        <div className="room-image"><img src={studioo} /></div>
                        <div className="room-title">Sala mniejsza</div>
                        <hr style={{width: 100}}></hr>
                        <div className="room-description">Sala mniejsza</div>
                    </div>
                    <div className="room" onClick={()=>{
                        setBigger(false)
                        setSmaller(false)
                        setStudio(true)
                        }}>
                        <div className="room-image"><img src={studioo} /></div>
                        <div className="room-title">Studio nagrań</div>
                        <hr style={{width: 100}}></hr>
                        <div className="room-description">Studio nagrań</div>
                    </div>

                </div>
            </div>
    )
}

export default Stuff
