import React, {useState} from 'react'
import './offer.css'
import Stuff from './content/Stuff'
import Bigger from '../bigger/Bigger'
import Smaller from '../smaller/Smaller'
import Studio from '../rehearsal/Studio'

const Offer = () => {
    const [bigger, setBigger] = useState(false)
    const [smaller, setSmaller] = useState(false)
    const [studio, setStudio] = useState(false)
    return (
        <div className="main-container section">
            <Stuff setBigger={setBigger} setSmaller={setSmaller} setStudio={setStudio}/>
            {bigger &&
            <Bigger/>}
            {smaller &&
            <Smaller/>}
            {studio && 
            <Studio/>}

        </div>
    )
}

export default Offer
