import React, {useState, useRef, useEffect} from 'react'
import './navbar.css'
import poland from './poland.png'
import uk from './uk.png'
import ukraine from './ukraine.png'
import logo from './logo1.png'
import { Link } from 'react-scroll'



const Navbar = () => {
    const [scrolled,setScrolled]=useState(false);
    const handleScroll=() => {
        const offset=window.scrollY;
        if(offset > 500 ){
          setScrolled(true);
        }
        else{
          setScrolled(false);
        }
      }
    
      useEffect(() => {
        window.addEventListener('scroll',handleScroll)
      })
    let navbarClasses=['navbar'];
      if(scrolled){
        navbarClasses.push('scrolled');
      }
    return (
      <header className={navbarClasses.join(" ")}>
        
        <div className="navbar">
            <div className="navbar-left">
                <div className="titlee"><img src={logo}/></div>
            </div>
            <div className="navbar-center" id="menu">
                <Link activeClass="active" to="head" spy={true} smooth={true}><div className="nav-link">Home</div></Link>
                <Link to="smaller" spy={true} smooth={true}><div className="nav-link">Oferta</div></Link>
                <Link to="pricing" spy={true} smooth={true}><div className="nav-link">Cennik</div></Link>
                <Link to="booking" spy={true} smooth={true}><div className="nav-link">Rezerwacje</div></Link>
                <Link to="regulamin" spy={true} smooth={true}><div className="nav-link">Regulamin</div></Link>
                <Link to="kontakt" spy={true} smooth={true}><div className="nav-link">Kontakt</div></Link>
                
            </div>
            <div className="navbar-right">
                <div><img className="icon" src={poland}></img></div>
                <div><img className="icon" src={uk}></img></div>
                <div><img className="icon" src={ukraine}></img></div>
            </div>
        </div>
        </header>
    )
}

export default Navbar
